.home{
	.home-hero{
		background: 
		url(../../images/logos/inhabitent-logo-full.svg) no-repeat center center, linear-gradient( to bottom, rgba(0,0,0,0.4) 0%, rgba(0,0,0,0.4) 100% ), url(../../images/home-hero.jpg) no-repeat center bottom;
		background-size: auto, cover, cover;
		height: 100vh;
	}
	.site-content{
		padding-top: 0;
	}
	.products-container{
		width: 1120px;
		margin: 0 auto 3rem;
	}
	.product-categories{
		display: flex;
		justify-content: space-between;
		text-align: center;
	}
	.prod-category-wrapper{
		border: 1px solid #e1e1e1;
		border-top-width: 5px;
		-webkit-flex: 0 1 23.5%;
		-ms-flex: 0 1 23.5%;
		flex: 0 1 23.5%;
		max-width: 23.5%;
		padding: 0.75rem 0.75rem 2rem;
		img{
			width: 75px;
		}
	}
	h2{
		font-size: 2.4rem;
		font-weight: 400;
		margin-bottom: 3.75rem;
		text-align: center;
	}
	.category-link{
		background: #248a83;
		border: none;
		border-radius: 0;
		color: #fff;
		cursor: pointer;
		font-family: Novecento Sans Web,sans-serif;
		font-size: .9rem;
		padding: .75rem 1.5rem;
		text-transform: uppercase;
	}
}

	.journal-container{
		width: 1120px;
		margin: 0 auto;
	.journal{
		display: flex;
		justify-content: space-between;
		margin-bottom: 3rem;
		.journal-post{
			flex-basis: 32%;
			border: 1px solid #e1e1e1;
		}
		.journal-img{
			background-color: #e1e1e1;
			height: 250px;
			overflow: hidden;
			position: relative;
		}
		img{
			left: 50%;
			max-height: 250px;
			max-width: none;
			position: absolute;
			top: 50%;
			-webkit-transform: translate(-50%,-52%);
			transform: translate(-50%,-52%);
			width: auto;
		}
		.journal-text-wrapper{
			flex-direction: column;
			justify-content: space-between;
			padding: .75rem;
			position: relative;
		}
		.read-more{
			position: absolute;
			bottom: .75rem;
			left: .75rem;
			border: 1px solid #000;
    		color: #000;
			padding: .75rem 1.5rem;
		}
		h3{
			font-size: 1.4rem;
   			margin-bottom: 3.75rem;
			text-transform: uppercase;
			font-family: 'NovecentoSans', sans-serif;
			line-height: 1.2;
		}
		.journal-meta{
			font-size: 0.75rem;
			margin: 0;
		}
	}
}

.adventures-container{
	width: 1120px;
	margin: 0 auto 4rem;
	img{
		position: absolute;
		max-width: none;
		top: 50%;
		height: 100%;
		width: auto;
		left: 50%;
		transform: translate(-50%,-50%);
	}
	.adventure-post{
		position: relative;
		float: left;
		overflow: hidden;
	}
	.adventure-post:first-child{
		height: 540px;
		width: 540px;
		margin-right: 20px;
		h3{
			font-size: 3rem;
			font-weight: 300;
		}
	}
	.adventure-post:nth-child(2){
		height: 250px;
		width: 560px;
		margin-bottom: 20px;
		img{
			width: 100%;
			height: auto;
		}
		h3{
			font-size: 2.4rem;
			font-weight: 300;
		}
	}
	.adventure-post:nth-child(3){
		height: 270px;
		width: 270px;
		margin-right: 20px;
		h3{
			font-size: 1.6rem;
			font-weight: 300;
		}
	}
	.adventure-post:nth-child(4){
		height: 270px;
		width: 270px;
		h3{
			font-size: 1.6rem;
			font-weight: 300;
		}
	}
	.adventure-text-wrapper{
		position: absolute;
		top: 0;
		margin: 1.5rem;
	}
	.clearfix:after {
		visibility: hidden;
		display: block;
		font-size: 0;
		content: " ";
		clear: both;
		height: 0;
	}
	h3{
		color: #fff;
		line-height: 1.2;
	}
	.read-more{
		position: absolute;
		bottom: .75rem;
		left: .75rem;
		border: 1px solid #fff;
		color: #fff;
		padding: .25rem 1.5rem;
		bottom: -25px;
	}
	.more-adventures{
		margin-top: 2.25rem;
		a{
			font-size: 1.2rem;
			background: #248a83;
			border: none;
			border-radius: 0;
			color: #fff;
			cursor: pointer;
			font-family: Novecento Sans Web,sans-serif;
			padding: .75rem 1.5rem;
			text-transform: uppercase;
		}
	}
}